import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import './ReservationStatus.css';

const ReservationStatus = ({ currentLanguage = 'ja' }) => {
  const navigate = useNavigate();
  const [reservationStats, setReservationStats] = useState([]);
  const [lastUpdated, setLastUpdated] = useState('');
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState('');

  // 
  const translations = {
    ja: {
      title: '',
      subtitle: '6',
      dateTime: '',
      venue: '',
      reservations: '',
      capacity: '',
      rate: '',
      details: '',
      detailBtn: '',
      lastUpdated: '',
      loading: '...',
      error: '',
      retry: '',
      noEvents: '',
      people: ''
    },
    en: {
      title: 'Reservation Status',
      subtitle: 'Check real-time reservation status for the next 6 events',
      dateTime: 'Date & Time',
      venue: 'Venue',
      reservations: 'Reservations',
      capacity: 'Capacity',
      rate: 'Rate',
      details: 'Details',
      detailBtn: 'View Details',
      lastUpdated: 'Last Updated',
      loading: 'Loading...',
      error: 'Failed to load data',
      retry: 'Retry',
      noEvents: 'No events',
      people: ''
    },
    vi: {
      title: 'Tnh Trng t Ch',
      subtitle: 'Kim tra tnh trng t ch theo thi gian thc cho 6 s kin tip theo',
      dateTime: 'Ngy & Gi',
      venue: 'a im',
      reservations: 't Ch',
      capacity: 'Sc Cha',
      rate: 'T L',
      details: 'Chi Tit',
      detailBtn: 'Xem Chi Tit',
      lastUpdated: 'Cp Nht Ln Cui',
      loading: 'ang ti...',
      error: 'Khng ti c d liu',
      retry: 'Th Li',
      noEvents: 'Khng c s kin',
      people: ''
    },
    de: {
      title: 'Reservierungsstatus',
      subtitle: 'berprfen Sie den Echtzeit-Reservierungsstatus fr die nchsten 6 Veranstaltungen',
      dateTime: 'Datum & Uhrzeit',
      venue: 'Veranstaltungsort',
      reservations: 'Reservierungen',
      capacity: 'Kapazitt',
      rate: 'Rate',
      details: 'Details',
      detailBtn: 'Details anzeigen',
      lastUpdated: 'Zuletzt aktualisiert',
      loading: 'Wird geladen...',
      error: 'Daten konnten nicht geladen werden',
      retry: 'Wiederholen',
      noEvents: 'Keine Veranstaltungen',
      people: ''
    },
    ko: {
      title: ' ',
      subtitle: ' 6     ',
      dateTime: '  ',
      venue: '',
      reservations: '',
      capacity: ' ',
      rate: '',
      details: '',
      detailBtn: ' ',
      lastUpdated: ' ',
      loading: ' ...',
      error: '  ',
      retry: ' ',
      noEvents: ' ',
      people: ''
    },
    zh: {
      title: '',
      subtitle: '6',
      dateTime: '',
      venue: '',
      reservations: '',
      capacity: '',
      rate: '',
      details: '',
      detailBtn: '',
      lastUpdated: '',
      loading: '...',
      error: '',
      retry: '',
      noEvents: '',
      people: ''
    },
    fr: {
      title: 'tat des Rservations',
      subtitle: 'Consultez l\'tat des rservations en temps rel pour les 6 prochains vnements',
      dateTime: 'Date & Heure',
      venue: 'Lieu',
      reservations: 'Rservations',
      capacity: 'Capacit',
      rate: 'Taux',
      details: 'Dtails',
      detailBtn: 'Voir les Dtails',
      lastUpdated: 'Dernire Mise  Jour',
      loading: 'Chargement...',
      error: 'chec du chargement des donnes',
      retry: 'Ressayer',
      noEvents: 'Aucun vnement',
      people: ''
    }
  };

  const t = translations[currentLanguage];

  const GAS_WEB_APP_URL = 'https://script.google.com/macros/s/AKfycbxZRZSDGyg_Z1rGcuD9xymlMXB4vV3Cz8EVTOWS2GvP-bLKeYcq7q122ixPQKV71Xg6iQ/exec';

  // 
  const fetchReservationStats = async () => {
    try {
      setError('');
      // 
      const url = `${GAS_WEB_APP_URL}?action=getReservationStats&_t=${Date.now()}`;

      const response = await fetch(url);

      if (!response.ok) {
        throw new Error('');
      }

      const result = await response.json();

      if (result.success) {
        // GAS v3.10 data 
        const reservations = result.data?.reservations || result.reservations || [];
        const lastUpdated = result.data?.last_updated || result.last_updated || '';


        setReservationStats(reservations);
        setLastUpdated(lastUpdated);
      } else {
        throw new Error(result.message || '');
      }

      setIsLoading(false);
    } catch (err) {
      setError('');
      setIsLoading(false);
    }
  };

  // 30
  useEffect(() => {
    fetchReservationStats();

    // 30
    const intervalId = setInterval(() => {
      fetchReservationStats();
    }, 30000);

    // 
    return () => clearInterval(intervalId);
  }, []);

  // 
  const getStatusColor = (rate) => {
    if (rate >= 90) return 'red';
    if (rate >= 70) return 'yellow';
    return 'green';
  };

  if (isLoading) {
    return (
      <section className="reservation-status">
        <div className="section-container">
          <h2 className="section-title">{t.title}</h2>
          <p className="section-subtitle">{t.subtitle}</p>
          <div className="reservation-status-content">
            <div className="loading-message">
              <div className="spinner"></div>
              <p>{t.loading}</p>
            </div>
          </div>
        </div>
      </section>
    );
  }

  if (error) {
    return (
      <section className="reservation-status">
        <div className="section-container">
          <h2 className="section-title">{t.title}</h2>
          <p className="section-subtitle">{t.subtitle}</p>
          <div className="reservation-status-content">
            <div className="error-message">
              <p>{t.error}</p>
              <button onClick={fetchReservationStats} className="retry-btn">
                {t.retry}
              </button>
            </div>
          </div>
        </div>
      </section>
    );
  }

  return (
    <section className="reservation-status">
      <div className="section-container">
        <h2 className="section-title">{t.title}</h2>
        <p className="section-subtitle">{t.subtitle}</p>

        {reservationStats.length === 0 ? (
          <div className="reservation-status-content">
            <div className="no-events-message">
              <p>{t.noEvents}</p>
            </div>
          </div>
        ) : (
          <>
            <div className="table-container">
              <table className="reservation-table">
                <thead>
                  <tr>
                    <th>{t.dateTime}</th>
                    <th>{t.venue}</th>
                    <th className="center">{t.reservations}</th>
                    <th className="center">{t.capacity}</th>
                    <th className="center">{t.rate}</th>
                    <th className="center">{t.details}</th>
                  </tr>
                </thead>
                <tbody>
                  {reservationStats.map((stat, index) => {
                    const statusColor = getStatusColor(stat.reservation_rate);

                    return (
                      <tr key={index}>
                        <td data-label={t.dateTime} className="date-cell">
                          {stat.event_info}
                        </td>
                        <td data-label={t.venue} className="venue-cell">
                          {stat.venue}
                        </td>
                        <td data-label={t.reservations} className="center number-cell">
                          {stat.current_reservations}{t.people}
                        </td>
                        <td data-label={t.capacity} className="center number-cell">
                          {stat.capacity}{t.people}
                        </td>
                        <td data-label={t.rate} className="center rate-cell">
                          <div className="rate-bar-container">
                            <div className="rate-bar">
                              <div
                                className={`rate-fill rate-${statusColor}`}
                                style={{ width: `${Math.min(stat.reservation_rate, 100)}%` }}
                              ></div>
                            </div>
                            <span className="rate-text">{stat.reservation_rate}%</span>
                          </div>
                        </td>
                        <td data-label={t.details} className="center detail-cell">
                          <button
                            className="detail-btn"
                            onClick={() => window.open(`/ke/reservations/${stat.event_id}?lang=${currentLanguage}`, '_blank')}
                            disabled={stat.current_reservations === 0}
                          >
                            {t.detailBtn}
                          </button>
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            </div>

            {lastUpdated && (
              <div className="last-updated">
                <p>{t.lastUpdated}: {lastUpdated}</p>
              </div>
            )}
          </>
        )}
      </div>
    </section>
  );
};

export default ReservationStatus;
