import React, { useState } from 'react';
import './ReservationList.css';

const ReservationList = ({ reservations, onRefresh }) => {
  const [searchTerm, setSearchTerm] = useState('');
  const [filterStatus, setFilterStatus] = useState('all');
  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 10;

  // 
  const filteredReservations = reservations.filter(reservation => {
    const matchesSearch =
      reservation.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      reservation.email.toLowerCase().includes(searchTerm.toLowerCase()) ||
      (reservation.eventVol && reservation.eventVol.toLowerCase().includes(searchTerm.toLowerCase())) ||
      (reservation.eventArea && reservation.eventArea.toLowerCase().includes(searchTerm.toLowerCase()));

    const matchesStatus =
      filterStatus === 'all' || reservation.status === filterStatus;

    return matchesSearch && matchesStatus;
  });

  // 
  const totalPages = Math.ceil(filteredReservations.length / itemsPerPage);
  const startIndex = (currentPage - 1) * itemsPerPage;
  const endIndex = startIndex + itemsPerPage;
  const currentReservations = filteredReservations.slice(startIndex, endIndex);

  // CSV
  const handleExportCSV = () => {
    const headers = ['ID', '', '', '', '', '', '', '', '', '', '', '', '', '', ''];
    const csvData = filteredReservations.map(r => [
      r.id,
      r.reservationDate,
      r.eventDate,
      r.eventTime,
      r.eventName,
      r.eventArea,
      r.eventVol,
      r.eventCapacity,
      r.name,
      r.email,
      r.companionCount,
      r.arrivalTime,
      r.desiredGame,
      r.notes,
      r.status
    ]);

    const csvContent = [
      headers.join(','),
      ...csvData.map(row => row.map(cell => `"${cell}"`).join(','))
    ].join('\n');

    const blob = new Blob(['\uFEFF' + csvContent], { type: 'text/csv;charset=utf-8;' });
    const link = document.createElement('a');
    link.href = URL.createObjectURL(blob);
    link.download = `reservations_${new Date().toISOString().split('T')[0]}.csv`;
    link.click();
  };

  return (
    <div className="reservation-list">
      {/*  */}
      <div className="filter-bar">
        <input
          type="text"
          className="search-input"
          placeholder="ID..."
          value={searchTerm}
          onChange={(e) => {
            setSearchTerm(e.target.value);
            setCurrentPage(1);
          }}
        />

        <select
          className="status-filter"
          value={filterStatus}
          onChange={(e) => {
            setFilterStatus(e.target.value);
            setCurrentPage(1);
          }}
        >
          <option value="all"></option>
          <option value="confirmed"></option>
          <option value="cancelled"></option>
        </select>

        <button className="export-btn" onClick={handleExportCSV}>
           CSV
        </button>
      </div>

      {/*  */}
      <div className="reservation-count">
        {filteredReservations.length}
      </div>

      {/*  */}
      {currentReservations.length > 0 ? (
        <div className="table-container">
          <table className="reservation-table">
            <thead>
              <tr>
                <th>ID</th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {currentReservations.map((reservation) => (
                <tr key={reservation.id}>
                  <td className="id-cell">#{reservation.id}</td>
                  <td className="event-cell">{reservation.eventDate}</td>
                  <td className="event-cell">{reservation.eventArea}</td>
                  <td className="event-cell">{reservation.eventVol}</td>
                  <td className="name-cell">{reservation.name}</td>
                  <td className="email-cell">{reservation.email}</td>
                  <td className="companion-cell">{reservation.companionCount}</td>
                  <td className="date-cell">{reservation.reservationDate}</td>
                  <td className="status-cell">
                    <span className={`status-badge status-${reservation.status}`}>
                      {reservation.status === 'confirmed' ? '' : ''}
                    </span>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      ) : (
        <div className="empty-table">
          <p></p>
        </div>
      )}

      {/*  */}
      {totalPages > 1 && (
        <div className="pagination">
          <button
            className="pagination-btn"
            onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))}
            disabled={currentPage === 1}
          >
             
          </button>

          <span className="pagination-info">
             {currentPage} / {totalPages}
          </span>

          <button
            className="pagination-btn"
            onClick={() => setCurrentPage(prev => Math.min(prev + 1, totalPages))}
            disabled={currentPage === totalPages}
          >
             
          </button>
        </div>
      )}
    </div>
  );
};

export default ReservationList;
