import React, { useState, useRef } from 'react';
import './Contact.css';
import { trackContactFormSubmit } from '../utils/gtm';

const Contact = () => {
  const form = useRef();
  const [isLoading, setIsLoading] = useState(false);
  const [message, setMessage] = useState('');

  const sendEmail = (e) => {
    e.preventDefault();
    setIsLoading(true);

    // GAS WebURL
    const GAS_WEB_APP_URL = process.env.REACT_APP_GAS_WEB_APP_URL || 'https://script.google.com/macros/s/AKfycbwGhOV6W4DoMTK9Zagbdjqq0KVx0KVThPqFtIzbFG__fine1Kez4_EmO7G9TwMiYrIGbg/exec';

    // HTMLCORS
    const hiddenForm = document.createElement('form');
    hiddenForm.method = 'POST';
    hiddenForm.action = GAS_WEB_APP_URL;
    hiddenForm.target = '_blank'; // 

    // 
    const formData = {
      formType: 'home',
      user_name: form.current.user_name.value,
      user_email: form.current.user_email.value,
      user_phone: form.current.user_phone.value,
      inquiry_type: form.current.inquiry_type.value,
      message: form.current.message.value
    };

    Object.keys(formData).forEach(key => {
      const input = document.createElement('input');
      input.type = 'hidden';
      input.name = key;
      input.value = formData[key] || '';
      hiddenForm.appendChild(input);
    });

    // 
    document.body.appendChild(hiddenForm);
    hiddenForm.submit();
    document.body.removeChild(hiddenForm);

    // GTM - 
    trackContactFormSubmit({
      name: formData.user_name,
      email: formData.user_email,
      inquiry_type: formData.inquiry_type
    });

    // 
    setMessage('');
    form.current.reset();
    setIsLoading(false);
  };

  return (
    <section id="contact" className="contact section">
      <div className="section-container">
        <h2 className="section-title"></h2>
        <p className="section-subtitle">
          
        </p>

        <div className="contact-content">
          <div className="form-card">
            <h3 className="contact-title"></h3>
            
            <form ref={form} onSubmit={sendEmail} className="contact-form-content">
              <div className="form-group">
                <label htmlFor="name"> <span className="required">*</span></label>
                <input 
                  type="text" 
                  id="name" 
                  name="user_name" 
                  required 
                  disabled={isLoading}
                />
              </div>

              <div className="form-group">
                <label htmlFor="email"> <span className="required">*</span></label>
                <input 
                  type="email" 
                  id="email" 
                  name="user_email" 
                  required 
                  disabled={isLoading}
                />
              </div>

              <div className="form-group">
                <label htmlFor="phone"></label>
                <input 
                  type="tel" 
                  id="phone" 
                  name="user_phone" 
                  disabled={isLoading}
                />
              </div>

              <div className="form-group">
                <label htmlFor="subject"> <span className="required">*</span></label>
                <select 
                  id="subject" 
                  name="inquiry_type" 
                  required 
                  disabled={isLoading}
                >
                  <option value=""></option>
                  <option value="event">Ke.</option>
                  <option value="collaboration"></option>
                  <option value="sponsorship"></option>
                  <option value="game-development"></option>
                  <option value="training"></option>
                  <option value="other"></option>
                </select>
              </div>

              <div className="form-group">
                <label htmlFor="message"> <span className="required">*</span></label>
                <textarea 
                  id="message" 
                  name="message" 
                  rows="6" 
                  required 
                  disabled={isLoading}
                ></textarea>
              </div>

              {message && (
                <div className={`form-message ${message.includes('') ? 'error' : 'success'}`}>
                  {message}
                </div>
              )}

              <button 
                type="submit" 
                className={`btn ${isLoading ? 'loading' : ''}`}
                disabled={isLoading}
              >
                {isLoading ? '...' : ''}
              </button>
            </form>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Contact; 