import React, { useState, useEffect } from 'react';
import { Helmet } from 'react-helmet-async';
import Header from '../components/Header';
import Footer from '../components/Footer';
import BackToTop from '../components/BackToTop';
import '../styles/BasePage.css';
import './NewsPage.css';

// 
const ImageSlider = ({ imageUrls, title }) => {
  const [currentSlide, setCurrentSlide] = useState(0);
  
  const nextSlide = () => {
    setCurrentSlide((prev) => (prev + 1) % imageUrls.length);
  };
  
  const prevSlide = () => {
    setCurrentSlide((prev) => (prev - 1 + imageUrls.length) % imageUrls.length);
  };
  
  const goToSlide = (index) => {
    setCurrentSlide(index);
  };
  
  return (
    <div className="news-card-images">
      <div className="news-images-slider">
        <div 
          className="news-images-track" 
          style={{ transform: `translateX(-${currentSlide * 100}%)` }}
        >
          {imageUrls.map((url, index) => (
            <div key={index} className="news-image-slide">
              <img src={url} alt={`${title} - ${index + 1}`} />
            </div>
          ))}
        </div>
        
        {/*  */}
        <div className="news-images-counter">
          {currentSlide + 1} / {imageUrls.length}
        </div>
        
        {/*  */}
        <button 
          className="news-images-arrow news-images-arrow-prev"
          onClick={prevSlide}
          aria-label=""
        >
          <svg width="24" height="24" viewBox="0 0 24 24" fill="none">
            <path d="M15 18L9 12L15 6" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
          </svg>
        </button>
        
        {/*  */}
        <button 
          className="news-images-arrow news-images-arrow-next"
          onClick={nextSlide}
          aria-label=""
        >
          <svg width="24" height="24" viewBox="0 0 24 24" fill="none">
            <path d="M9 6L15 12L9 18" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
          </svg>
        </button>
        
        {/*  */}
        <div className="news-images-nav">
          {imageUrls.map((_, index) => (
            <button
              key={index}
              className={`news-images-dot ${currentSlide === index ? 'active' : ''}`}
              onClick={() => goToSlide(index)}
              aria-label={` ${index + 1}`}
            />
          ))}
        </div>
      </div>
    </div>
  );
};

const NewsPage = () => {
  const [selectedCategory, setSelectedCategory] = useState('all');
  const [newsItems, setNewsItems] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  // NEWS API URLGAS URL
  const NEWS_API_URL = 'https://script.google.com/macros/s/AKfycbymI6FuKRcoFu6BP558Dwj7RQFYf1sCDm5dWhHdmHJt6ibEdlseflU-0krlqL2mAG7_/exec';

  // NEWS 
  useEffect(() => {
    const fetchNews = async () => {
      setIsLoading(true);
      setError(null);

      try {
        const newsUrl = `${NEWS_API_URL}?action=getPublishedNews`;
        const response = await fetch(newsUrl);
        const result = await response.json();

        if (result.success) {
          setNewsItems(result.data || []);
        } else {
          setError('');
        }
      } catch (err) {
        console.error('NEWS:', err);
        setError('');
        // : 
        setNewsItems([]);
      } finally {
        setIsLoading(false);
      }
    };

    fetchNews();
  }, []);

  // API
  const fallbackNewsItems = [
    {
      id: 1,
      date: '2025.07.14',
      category: '',
      title: 'Ke.',
      description: '72610:00Ke.',
      content: '3',
      isNew: true,
      link: '/ke'
    },
    {
      id: 2,
      date: '2025.07.13',
      category: '',
      title: 'YOLUBE',
      description: '',
      content: '',
      isNew: true
    },
    {
      id: 3,
      date: '2025.07.05',
      category: '',
      title: 'YOLUBE',
      description: '',
      content: '',
      isNew: false
    },
    {
      id: 4,
      date: '2025.06.28',
      category: '',
      title: '',
      description: '',
      content: 'SNS',
      isNew: false
    },
    {
      id: 5,
      date: '2025.06.15',
      category: '',
      title: '',
      description: '',
      content: '',
      isNew: false,
      link: '/training'
    },
    {
      id: 6,
      date: '2025.06.01',
      category: '',
      title: '',
      description: '50',
      content: '',
      isNew: false
    },
    {
      id: 7,
      date: '2025.05.20',
      category: '',
      title: 'ABS',
      description: 'YOLUBE',
      content: '',
      isNew: false
    },
    {
      id: 8,
      date: '2025.05.10',
      category: '',
      title: 'YOLUBE1',
      description: '1',
      content: '1500',
      isNew: false
    }
  ];

  const categories = [
    { value: 'all', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' }
  ];

  const filteredNews = selectedCategory === 'all'
    ? newsItems
    : newsItems.filter(item => item.category === selectedCategory);

  return (
    <>
      <Helmet>
        <title> | YOLUBE</title>
        <meta name="description" content="YOLUBE" />
      </Helmet>

      <Header />

      <div className="base-page">
        {/*  */}
        <section className="base-hero">
          <div className="container">
            <h1 className="base-hero-title"></h1>
          </div>
        </section>

        {/*  */}
        <section className="news-filter-section">
          <div className="container">
            <div className="news-filter">
              {categories.map(cat => (
                <button
                  key={cat.value}
                  className={`filter-btn ${selectedCategory === cat.value ? 'active' : ''}`}
                  onClick={() => setSelectedCategory(cat.value)}
                >
                  {cat.label}
                </button>
              ))}
            </div>
          </div>
        </section>

        {/*  */}
        <section className="news-list-section">
          <div className="container">
            {/*  */}
            {isLoading && (
              <div className="news-loading">
                <div className="news-spinner"></div>
                <p>...</p>
              </div>
            )}

            {/*  */}
            {error && !isLoading && (
              <div className="news-error">
                <p>{error}</p>
                <button onClick={() => window.location.reload()} className="news-reload-btn">
                  
                </button>
              </div>
            )}

            {/*  */}
            {!isLoading && !error && (
              <>
                <div className="news-grid">
                  {filteredNews.map(item => (
                    <article key={item.id} className={`news-card ${item.imageUrl ? 'news-card-with-image' : 'news-card-text-only'}`}>
                      <div className="news-card-header">
                        <div className="news-card-labels">
                          <span className={`news-card-category category-${item.category}`}>
                            {item.category}
                          </span>
                          {item.isNew && <span className="news-card-badge">NEW</span>}
                        </div>
                        <time className="news-card-date">{item.date}</time>
                      </div>

                      {item.imageUrl && (() => {
                        // 
                        const imageUrls = item.imageUrl.split(',').map(url => url.trim()).filter(url => url);
                        
                        if (imageUrls.length === 1) {
                          // 
                          return (
                            <div className="news-card-image">
                              <img src={imageUrls[0]} alt={item.title} />
                            </div>
                          );
                        } else if (imageUrls.length > 1) {
                          // 
                          return <ImageSlider imageUrls={imageUrls} title={item.title} />;
                        }
                        return null;
                      })()}

                      <h2 className="news-card-title">{item.title}</h2>
                      <p className="news-card-description">{item.description}</p>
                      <p className="news-card-content">{item.content}</p>

                      {item.link && (
                        <a href={item.link} className="news-card-link">
                          
                          <svg width="16" height="16" viewBox="0 0 24 24" fill="none">
                            <path d="M9 18L15 12L9 6" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                          </svg>
                        </a>
                      )}
                    </article>
                  ))}
                </div>

                {filteredNews.length === 0 && (
                  <div className="news-empty">
                    <p></p>
                  </div>
                )}
              </>
            )}
          </div>
        </section>
      </div>

      <Footer />
      <BackToTop />
    </>
  );
};

export default NewsPage;
